import { ReactNode, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import Box from "@mui/material/Box";
import { PixyButton, PixyNFTCard } from "../components/pixyComponents";
import { BuyNFTModal } from "../components/BuyNFTModal/BuyNFTModal";
import { MockNFTDatastore } from "../MockNFTDatastore";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../components/pixyComponents/typography/TypographyTitle";
import TypographySubtitle, { TYPOGRAPHY_SUBTITLE_KIND } from "../components/pixyComponents/typography/TypographySubtitle";
import TypographyBody
  from "../components/pixyComponents/typography/TypographyBody";

export interface CampaignNFTProps {
  children: ReactNode;
}

type CampaignNFTParams = {
  campaignSlug: string;
  nftSlug: string;
};

export const CampaignVideo = () => {
  const [modalOpen, setModalOpen] = useState(false);

  const { campaignSlug, nftSlug } = useParams<CampaignNFTParams>();
  const nft = MockNFTDatastore[nftSlug];
  const imageUrl = nft ? nft.image : "";
  const title = nft ? nft.title : "UNTITLED";
  const ustPrice = nft ? nft.ustPrice : "9999999999";
  const isVariablePrice = nft ? nft.isVariablePrice : false;
  const shortDescription = nft ? nft.shortDescription : "";
  const description = nft ? nft.description : "";
  const history = useHistory();

  return (
    <>
      <Box
        component="section"
        sx={{
          display: "flex",
          margin: "100px auto 32px auto",
          width: 1322
        }}
      >
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary} sx={{marginBottom: "60px"}}>
          Video
        </TypographyTitle>
      </Box>
    </>
  );
};

export default CampaignVideo;
